/**
 * Secondary navigation
 *
 * @module patterns/navigation/navigation-primary
 * @author Web Development
 * @copyright City, University of London 2019
 */
import { toBool, appendAll } from '../../util';
import aria from '../../aria-attributes';

const className = 'navigation',
    classNameSpecific = className + '--primary',
    topLevel = classNameSpecific + '__section-text',
    buttonClassName = className + '__button',
    buttonIconClassName = buttonClassName + '__icon',
    openText = 'Open dropdown',
    closeText = 'Close dropdown';

/**
 * Transform the Matrix-generated navigation lists into an interactive menu.
 *
 * @param {HTMLElement} navigation - The primary navigation element.
 */
function launchPrimaryNavigation(navigation) {
    const topLevels = Array.from(navigation.querySelectorAll(`.${topLevel}`));

    topLevels.forEach(el => {
        const listItem = el.parentNode,
            button = document.createElement('button'),
            buttonDiv = document.createElement('div'),
            iconSpan = document.createElement('span');

        el.removeAttribute('tabindex');
        button.setAttribute(aria.expanded, 'false');
        iconSpan.setAttribute(aria.label, openText);
        iconSpan.classList.add(buttonIconClassName);
        iconSpan.classList.add('fas');
        iconSpan.classList.add('fa-fw');

        button.addEventListener(
            'click',
            () => {
                if (toBool(button.getAttribute(aria.expanded))) {
                    button.setAttribute(aria.expanded, 'false');
                    iconSpan.setAttribute(aria.label, openText);
                } else {
                    Array.from(navigation.querySelectorAll('button')).forEach(
                        button => button.setAttribute(aria.expanded, 'false')
                    );
                    Array.from(
                        navigation.querySelectorAll(`.${buttonIconClassName}`)
                    ).forEach(span => span.setAttribute(aria.label, openText));
                    button.setAttribute(aria.expanded, 'true');
                    iconSpan.setAttribute(aria.label, closeText);
                }
            },
            true
        );

        appendAll(buttonDiv, [el, iconSpan]);
        button.append(buttonDiv);
        listItem.prepend(button);
    });
}

export default {
    launchFn: launchPrimaryNavigation,
    launchQuery: `.${classNameSpecific}`,
};
